import React from 'react'
import styled from 'styled-components'

import Section from '../components/Section'

import RadioMegaLogo from './radio-mega.png'

const Div = styled.div`
  display: flex;
  @media (max-width: 800px) {
    flex-direction: column;
    text-align: center;
  }
`

const P = styled.p`
  padding-left: 1em;
`

const Img = styled.img`
  filter: invert(1);
  @media (max-width: 800px) {
    height: 200px;
    margin: 1.5em auto;
    width: 200px;
  }
`

const LaRadio = () => (
  <Section
    title="La radio"
    background="black"
    color="white"
    align="right"
    id="la-radio"
  >
    <Div>

      <Img src={RadioMegaLogo} alt="Radio Mega Logo" />
      <P>
        Les radios libres ont plus de trente ans. Depuis 1981, laccs aux mdias radiophoniques sest dmocratis. Mdia de communication sociale de proximit de lagglomration Valentinoise, Radio Mga promeut linformation locale, lexpression et la pratique de la radiodiffusion par tous et pour tous.
      </P>
    </Div>
  </Section>
)

export default LaRadio



